<template>
  <div class="tinymce-box fit">
    <editor v-model="myValue" :init="init" :disabled="disabled" @click="onClick"> </editor>
  </div>
</template>

<script>
//  http://tinymce.ax-z.cn/
// 
import tinymce from 'tinymce/tinymce' // tinymcehidden
import Editor from '@tinymce/tinymce-vue'

// jscss
import 'tinymce/skins/content/default/content.css'
import 'tinymce/themes/silver/theme.min.js'
// import 'tinymce/icons/default/icons' // icons.js Unexpected token '<'

// plugins
// https://www.tiny.cloud/docs/plugins/
import 'tinymce/plugins/image' // 
import 'tinymce/plugins/media' // 
import 'tinymce/plugins/table' // 
import 'tinymce/plugins/lists' // 
import 'tinymce/plugins/wordcount' // 
import 'tinymce/plugins/link'
import 'tinymce/plugins/code'
import 'tinymce/plugins/preview'
import 'tinymce/plugins/fullscreen'
import 'tinymce/plugins/help'
export default {
  components: {
    Editor,
  },
  name: 'Tinymce',
  props: {
    // 
    value: {
      type: String,
      default: '',
    },
    // 
    // abc.com/tinymcebaseUrltinymce
    baseUrl: {
      type: String,
      default: window.location.origin ? window.location.origin : '',
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    plugins: {
      type: [String, Array],
      default: 'link lists image code table wordcount media preview fullscreen help',
    },
    toolbar: {
      type: [String, Array],
      default:
        'bold italic underline strikethrough | fontsizeselect | formatselect | forecolor backcolor | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent blockquote | undo redo | link unlink code lists table image media | removeformat | fullscreen preview',
    },
  },
  data() {
    return {
      init: {
        language_url: 'tinymce/langs/zh_CN.js',
        // language_url: `${this.baseUrl}/tinymce/langs/zh_CN.js`,
        language: 'zh_CN',
        skin_url: 'tinymce/skins/ui/oxide',
        // skin_url: `${this.baseUrl}/tinymce/skins/ui/oxide`,
        // skin_url: 'tinymce/skins/ui/oxide-dark', // 
        convert_urls: false,
        height: '100%',
        // content_csscss,
        // content_css: `${this.baseUrl}tinymce/skins/content/default/content.css`,
        // 
        plugins: this.plugins,
        toolbar: this.toolbar, // 

        statusbar: true, // 
        menubar: 'file edit insert view format table tools help', // 1
        branding: false, // Powered by TinyMCE
        // base64
        // ajaxhttps://www.tiny.cloud/docs/configure/file-image-upload/#images_upload_handler
        images_upload_handler: (blobInfo, success, failure) => {
          const img = 'data:image/jpeg;base64,' + blobInfo.base64()
          success(img)
          console.log(failure)
        },
      },
      myValue: this.value,
    }
  },
  mounted() {
    console.log(window)
    tinymce.init({})
  },
  methods: {
    // => https://github.com/tinymce/tinymce-vue => All available events
    // 
    onClick(e) {
      this.$emit('onClick', e, tinymce)
    },
    // 
    clear() {
      this.myValue = ''
    },
  },
  watch: {
    value(newValue) {
      this.myValue = newValue
    },
    myValue(newValue) {
      this.$emit('input', newValue)
    },
  },
}
</script>
<style scoped></style>
