<script>
import tinymce from '@/components/tinymce'
export default {
  components: {
    tinymce,
  },
  data() {
    return {
      content: '',
      selectedNodeText: '',
      form: {
        id: 1,
        publishTime: '2021-04-20 13:30',
        materialType: 2,
        title: 'B 01',
        cate: '',
        remark: '',
        type: 1,
        staus: 2,
      },
      expandedKeys: ['01', '02', '03', '04', '05', '06', '6-3'],
      contentMap: {
        '01': '',
        '02': ` <p></p>
                <p>1 LCD1286460</p>
                <p>2 120</p>
                <p>3 180</p>`,
        '05': '',
        '5-1': `<p> 3x3</p>
                <p> </p>
                <p> 3x3</p>
                <p> </p>
                <p> </p>`,
        '5-2': `<p> </p>
                <p> SW16/</p>
                <p> </p>
                <p> </p>`,
        '5-3': 'CCCCCCCCCCCC',
        '6-1': `<p>LCD12864lcd12864.clcd12864.h</p>
                <p> 1.1</p>
                <p> </p>
                <p> 11111AB0 A H  LCD  MCUL  MCU  LCD B H L </p>
                <p>()8  4  DDDD0000</p>
                <p>()8  4  DDDD0000</p>
                <p>ST792026</p>
                <p>void sendFrame(uint32_t data)LCD12864data24bit</p>
                <p>1.2</p>
                <p>LCD128641.1sendFrame:</p>
                <p> void writeCmd(uint8_t cByte)LCD12864cByte</p>
                <p> void writeData(uint8_t dByte) LCD12864dByte</p>
                <p>user_function.csetupLCDLCDLCD12864</p>`,
        '6-1-1': ' ',
        '6-3-1': '<p>LCD: MM:SS ,MMSS0</p>',
      },
      nodeList: [
        { key: '01', title: '' },
        { key: '02', title: '' },
        { key: '03', title: '' },
        { key: '04', title: '', children: [] },
        {
          key: '05',
          title: '',
          children: [
            { key: '5-1', title: '' },
            { key: '5-2', title: '' },
            { key: '5-3', title: '', isLeaf: true },
          ],
        },
        {
          key: '06',
          title: '',
          children: [
            { key: '6-1', title: '' },
            { key: '6-2', title: '' },
            {
              key: '6-3',
              title: '',
              children: [
                { key: '6-3-1', title: ' 3.1', children: [] },
                { key: '6-3-2', title: ' 3.2', children: [] },
                { key: '6-3-3', title: ' 3.3', children: [] },
              ],
            },
          ],
        },
      ],
      timer: null,
    }
  },
  methods: {
    onCateChange() {},
    onSubmit() {},
    onDragEnter() {},
    onDrop(info) {
      console.log(info)
      const dropKey = info.node.eventKey
      const dragKey = info.dragNode.eventKey
      const dropPos = info.node.pos.split('-')
      const dropPosition = info.dropPosition - Number(dropPos[dropPos.length - 1])
      const loop = (data, key, callback) => {
        data.forEach((item, index, arr) => {
          if (item.key === key) {
            return callback(item, index, arr)
          }
          if (item.children) {
            return loop(item.children, key, callback)
          }
        })
      }
      const data = [...this.gData]

      // Find dragObject
      let dragObj
      loop(data, dragKey, (item, index, arr) => {
        arr.splice(index, 1)
        dragObj = item
      })
      if (!info.dropToGap) {
        // Drop on the content
        loop(data, dropKey, (item) => {
          item.children = item.children || []
          // where to insert 
          item.children.push(dragObj)
        })
      } else if (
        (info.node.children || []).length > 0 && // Has children
        info.node.expanded && // Is expanded
        dropPosition === 1 // On the bottom gap
      ) {
        loop(data, dropKey, (item) => {
          item.children = item.children || []
          // where to insert 
          item.children.unshift(dragObj)
        })
      } else {
        let ar
        let i
        loop(data, dropKey, (item, index, arr) => {
          ar = arr
          i = index
        })
        if (dropPosition === -1) {
          ar.splice(i, 0, dragObj)
        } else {
          ar.splice(i + 1, 0, dragObj)
        }
      }
      this.nodeList = data
    },
    onEditorClick(e, editor) {
      console.log('Element clicked')
      console.log(e)
      console.log(editor)
    },
    onSelect(keys, data) {
      console.log('onSelect', keys, data.node.eventKey)
      const node = data.node
      this.content = this.contentMap[node.eventKey]
    },
    onContextMenu(e) {
      for (const i in e.path) {
        if (e.path[i].classList && e.path[i].classList.contains('ant-tree-node-content-wrapper')) {
          let rect = this.$refs.content.getBoundingClientRect()
          this.selectedNodeText = e.path[i].innerText
          console.log(e)
          this.$refs.nodeMenu.style.left = `${e.x - rect.left}px`
          this.$refs.nodeMenu.style.top = `${e.y - rect.top}px`
          this.$refs.nodeMenu.style.display = 'block'
          break
        }
      }
    },
    onMouseOut(e) {
      this.timer = setTimeout(() => {
        this.$refs.nodeMenu.style.display = 'none'
      }, 300)
    },
    onMouseOver(e) {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.$refs.nodeMenu.style.display = 'block'
    },
    toggleFullscreen() {
      this.fullscreen.toggle(this.$refs.content)
    },
  },
}
</script>

